/**
 * Loader path helper for Next.js configuration
 * This file provides the path to component-tagger-loader.js
 * 
 * IMPORTANT: This file is server-only and should only be imported in next.config.ts
 * Do not import this in client components or pages.
 */

import path from 'path'
import { fileURLToPath } from 'url'
import { createRequire } from 'module'

// Get the directory of this file (works for both ESM and CJS)
const getDirname = () => {
  try {
    // ESM
    return path.dirname(fileURLToPath(import.meta.url))
  } catch {
    // CJS fallback
    return __dirname
  }
}

const __dirname_actual = getDirname()

/**
 * Returns the absolute path to the component-tagger-loader.js file
 * Use this in your next.config.ts:
 * 
 * import { loaderPath } from 'orchids-visual-edits/loader'
 * 
 * const nextConfig = {
 *   turbopack: {
 *     rules: {
 *       '*.{jsx,tsx}': {
 *         loaders: [loaderPath]
 *       }
 *     }
 *   }
 * }
 */
export const loaderPath = (() => {
  try {
    // Try CommonJS require.resolve first
    const require = createRequire(import.meta.url)
    return require.resolve('./component-tagger-loader.js')
  } catch {
    // Fallback to path resolution
    return path.resolve(__dirname_actual, 'component-tagger-loader.js')
  }
})()
